// app/pratos/page.tsx
import Link from 'next/link';
import UnitsSection from '@/components/UnitsSection/UnitsSection';
import Header from '@/components/Header/Header';
import Footer from '@/components/Footer/Footer';
import CategoryCard from '@/components/CategoryCard/CategoryCard';

export default function PratosPage() {
  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <Header />

      {/* Seção Principal */}
      <main className="container mx-auto px-4 py-12">
        {/* Cabeçalho */}
        <div className="text-center mb-16">
          <h2 className="text-4xl font-bold text-emerald-700 mb-6">Conheça nossos pratos</h2>
          <p className="text-gray-600 max-w-2xl mx-auto">
            Oferecemos uma seleção de pratos especiais que combinam ingredientes frescos, sabores autênticos e toques exclusivos.
          </p>
        </div>

        {/* Grid de Categorias */}
        <div className="grid md:grid-cols-2 gap-12 mb-20">
          {/* Categorias de Pratos */}
          <div className="bg-white p-8 rounded-xl shadow-lg">
            <h3 className="text-2xl font-bold text-emerald-700 mb-6">Categorias de Pratos</h3>
            <div className="space-y-4">
              {['Entradas', 'Principais', 'Sobremesas'].map((categoria) => (
                <Link
                  key={categoria}
                  href={`/pratos/${categoria.toLowerCase()}`}
                  className="block p-4 hover:bg-emerald-50 rounded-lg transition-colors"
                >
                  <span className="text-lg text-gray-800">{categoria}</span>
                </Link>
              ))}
            </div>
          </div>

          {/* Categorias de Bebidas */}
          <div className="bg-white p-8 rounded-xl shadow-lg">
            <h3 className="text-2xl font-bold text-emerald-700 mb-6">Categorias de Bebidas</h3>
            <div className="space-y-4">
              {['Vinhos', 'Refrigerantes', 'Sucos'].map((bebida) => (
                <div
                  key={bebida}
                  className="p-4 hover:bg-emerald-50 rounded-lg transition-colors cursor-not-allowed"
                >
                  <span className="text-lg text-gray-400">{bebida} (em breve)</span>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Seção Unidades */}
        <UnitsSection />
      </main>

      <Footer />
    </div>
  );
}